import React, { Component } from 'react';
import ImageType from '../../../CommonComponents/MediaType/ImageType';
import GridHeader from './GridHeader';
import GridFooter from './GridFooter';

class Grid_2x3 extends Component {
    constructor() {
        super();
        this.state = { showModal: false };
    }
    render() {
        const { data, spaceVal } = this.props
        const gridRow = new Array(3).fill(0);
        const gridColumn = new Array(2).fill(0);
        let count = -1
        let url = ''
        return <div className="emaiPreviewInner">
            <GridHeader />
            <div className="row gx-3">
                <div className="col-5">
                    <div className="pb-5">
                        <div className="w-100 rounded-4 h-8px mb-2 bg-gray-200"></div>
                        <div className="w-100 rounded-4 h-8px mb-2 bg-gray-200"></div>
                        <div className="w-100 rounded-4 h-8px mb-2 bg-gray-200"></div>
                        <div className="w-100 rounded-4 h-8px mb-2 bg-gray-200"></div>
                        <div className="w-50 rounded-4 h-8px mb-4"></div>
                        <div className="w-100 h-26px mb-1 bg-gray-200" style={{ maxWidth: 90 }}></div>
                    </div>
                    <div className="pb-3">
                        <div className="w-100 rounded-4 h-8px mb-2 bg-gray-200"></div>
                        <div className="w-100 rounded-4 h-8px mb-2 bg-gray-200"></div>
                        <div className="w-100 rounded-4 h-8px mb-2 bg-gray-200"></div>
                        <div className="w-100 rounded-4 h-8px mb-2 bg-gray-200"></div>
                        <div className="w-50 rounded-4 h-8px mb-4"></div>
                        <div className="w-100 h-26px mb-1 bg-gray-200" style={{ maxWidth: 90 }}></div>
                    </div>
                </div>
                <div className="col-7">
                    <div className="mb-3 rounded-1 w-75 mx-auto">
                        <div className="p-1">

                            {
                                gridRow.map((grid, gridKey) => {
                                    return <div className="row gx-0">
                                        {
                                            gridColumn.map((grid1, index) => {
                                                count = count + 1;
                                                url = data[count]?.ugc_products?.length > 0 ? data[count].ugc_products[0].UgcProduct.product_url : data[count]?.link ? data[count]?.link : ''
                                                const mediaContent = (<ImageType mediaClass={'bg-gray-200'} size={100} height={300} width={300} url={data && data[count] && Object.keys(data[count]).length > 0 ? data[count].postFile : ``} isCover={true} alt={""} />);
                                                return url ? <a data-id={`media-${data[count]?.id}`} href={url} target='_blank' key={index} className="col media_col" title={'img'} style={{ padding: spaceVal / 2 }}>{mediaContent} </a>
                                                    : <span key={index} className="col media_col" style={{ padding: spaceVal / 2 }} > {mediaContent} </span>
                                            })
                                        }

                                    </div>
                                }
                                )}
                        </div>
                    </div>
                </div>
            </div>
            <GridFooter />
        </div>
    }
}

export default Grid_2x3;