import { Component } from "react";
import { Button, Row, Col } from "react-bootstrap";
import { baseUrl, FontAwesomeIcon } from "../../../../Assets/AssetsHelper";
import ImageType from "../CommonComponents/MediaType/ImageType";
import { GET_MEDIA_NAME, GET_NETWORK_NAME, GET_NETWORK_ICON } from "../../../../utils";

class Gallery extends Component{
    
    render(){
        const {selectContent, getPosts, postContent, showMore} = this.props
        return <div className="email_gallery">
                {selectContent && selectContent?.length > 0 ?
                <div className="p-3 w-100">
                    <Row className="gx-1">
                        {selectContent?.map((data, key) => {
                            return (<Col key={key} sm={4} lg={3} xl={4} xxl={3}>
                                        <div data-id={`media-${data.id}`} className="position-relative mb-1 cursor-pointer mail_media_" onClick={(e) => postContent(data)}>
                                            <ImageType mediaClass={'bg-gray-200'} size={100} height={300} width={300} url={data.postFile} isCover={true} alt={GET_MEDIA_NAME(data.postFile)} />
                                            {data.active ? <div className="position-absolute top-0 end-0 p-2">
                                                <span className="h-20px w-20px rounded-circle border border-light bg-primary f-center text-white fs-10">
                                                    <FontAwesomeIcon type={'solid'} icon={'check'} />
                                                </span>
                                            </div>:null}
                                            <div className="position-absolute p-2 end-0 bottom-0 social_icon">
                                                <img className="img-fluid" loading="lazy" src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(data.networkId)}.svg`)} height={16} width={16} alt={GET_NETWORK_NAME(data.networkId)} data-invert={GET_NETWORK_NAME(data.networkId)} />
                                            </div>
                                        </div>
                                    </Col>) })}
                    </Row>
                    {showMore ? <div className="f-center my-4">
                        <Button id="mail_more_btn" variant="link text-nowrap" size="sm" onClick={()=>getPosts(true)}>Show More</Button>
                    </div> : null}
                </div>
                :null}
            </div>
    }
}

export default Gallery