import React, { Component } from "react";
import { connect } from "react-redux";
import { CHANGE_EMAIL_WALL, GET_ALL_EMAIL_LIST, UPDATE_EMAIL_DATA } from "../../../../actions/EmailCampaignAction";
import { Button, Container, Row, Col, Card, Dropdown } from "react-bootstrap";
import { Link } from "react-router-dom";
import { baseUrl, FontAwesomeIcon } from "../../../../Assets/AssetsHelper";
import { Loader } from "../CommonComponents/Loader";
import AsideContent from "./Aside/AsideContent";
import Gallery from "./Gallery";
import Preview from "./Preview";
import CodePreview from "./CodePreview";
import { getUserInfo_afterChanges } from "../../../../actions/loginActions";
import CardAside from "../CommonComponents/CardAside";
import WebsiteSwitcher from "../CommonComponents/WebsiteSwitcher";
import ContentSwitcher from "../CommonComponents/ContentSwitcher";
import PlatformAside from "./Aside/PlatformAside";
import "./email-campaign.scss"
import withRouter from "../../../routes/WithRouter";
import _ from "lodash";
import EmptyWithIcon from "../CommonComponents/Empty/EmptyWithIcon";
import { TOGGLE_ADD_EMAIL } from "../../../../reducer/keys";
import SkGallery from "./SkeletonComponents/SkGallery";
import { checkPermission } from "../../../../actions/collaboratorActions";
import PublishSubheader from '../CommonComponents/PublishSubheader'

class EmailCampaign extends Component {
    state = { activeCamp: null, activeWall: null, spaceVal: 4, layout: 1, offSet: 0, campaignLayout: [], selectedPlatform: 'klaviyo', postError: false, preview: false, is_changes: false, spinner: false, filterToggle: false, selectContent: [], contentLoader: true, selectLoader: true, showMore: true }

    componentWillMount() {
        const { emailList, userData } = this.props
        if (!(emailList && emailList.length > 0)) this.props.getEmailList()
        else {
            const email = emailList.find(item => item.id == localStorage.getItem('emailCamp_id'))
            this.setEmailData(email)
        }
    }

    setEmailData = (email) => {
        const { allWalls, navigate } = this.props
        if (email && allWalls) {
            const { theme_type, spacing, post_id } = JSON.parse(email.extra_data)
            const wall = allWalls?.find(item => item.id == email.wallId)
            localStorage.setItem('emailCamp_id',email.id)
            this.setState({activeCamp: email, layout:theme_type, spaceVal:spacing, activeWall: wall ? wall : allWalls && allWalls.length > 0 ? allWalls[0] : {}, selectContent:[], offSet:0, campaignLayout:post_id && String(post_id).length > 0 ? post_id.split(',').map(image=>({postFile:image,active:1})) : [], selectLoader:false},()=>this.getPosts())    
        }
        else this.setState({ activeCamp: null, layout: 1, spaceVal: 4, activeWall: null, selectContent: [], offSet: 0, campaignLayout: [], selectLoader: true, contentLoader: true })

        navigate(`/email-campaign/publish/${localStorage.getItem('emailCamp_id') ? localStorage.getItem('emailCamp_id') : ''}`)
    }

    updateEmail = (val) => {
        const { emailList, navigate } = this.props
        const active = emailList?.find((x) => x.id == localStorage.getItem('emailCamp_id'));
        if (!val) {
            this.setState({ activeCamp: active ? active : emailList && emailList.length > 0 ? emailList[0] : '' })
            navigate(`/email-campaign/publish/${localStorage.getItem('emailCamp_id') ? localStorage.getItem('emailCamp_id') : ''}`)
        }
        else this.setEmailData(active)

    }

    componentDidUpdate(prevProps) {
        const { loader } = prevProps
        if ((loader != this.props.loader) || (!_.isEqual(prevProps.allWalls, this.props.allWalls) && this.props.emailList.length > 0)) {
            if (this.props.emailList.length > 0) {
                const email = this.props.emailList.find(item => item.id == localStorage.getItem('emailCamp_id'))
                this.setEmailData(email ? email : this.props.emailList[0])
            }
        }
    }

    changeWall = (e) => {
        const { allWalls } = this.props
        const wall = allWalls?.find(item => item.id == e.id)
        this.setState({ selectContent: [], offSet: 0, activeWall: wall ? wall : [], is_changes: true, campaignLayout: [] }, () => this.getPosts())
    }

    changeSpace = (e) => {
        this.setState({ spaceVal: e, is_changes: true })
    }

    selectLayout = (val) => this.setState({ layout: val, is_changes: true })

    getPosts = (set) => {
        const { offSet, selectContent, activeCamp, activeWall, campaignLayout } = this.state
        var extra_data = null
        if (selectContent.length <= 0) this.setState({ contentLoader: true })
        if (this.props.emailList && this.props.emailList.length && activeCamp && Object.keys(activeCamp).length > 0 && activeWall && Object.keys(activeCamp).length > 0) {

            const currentEmail = this.props.emailList.find(email => email.id == activeCamp?.id)
            extra_data = currentEmail ? JSON.parse(currentEmail.extra_data) : null

            CHANGE_EMAIL_WALL(activeWall.id, set ? offSet : 0).then((res) => {
                const { responseStatus } = res.data
                if (responseStatus) {
                    const resData = res.data.responseData;
                    let p_id = []
                    const myContent = resData.map((curr, index) => {

                        if (extra_data?.post_id && extra_data.post_id.split(',').includes(`${curr.postFile}`)) {
                            p_id.push(curr.postFile)
                            return { ...curr, 'active': 1, update: 1, }
                        }
                        else return { ...curr, 'active': 0, 'update': 0 }
                    })
                    if (resData.length) {
                        // this.setState({ selectContent: myContent, offSet: offSet + 20, dataLoad: false, campaignLayout: savedPost, addPostIds: p_id, contentLoading: false }, () => this.defaultSetting())
                        this.setState({ selectContent: [...selectContent, ...myContent], offSet: this.state.offSet + 20, dataLoad: false, addPostIds: p_id, contentLoading: false, showMore: true })
                    }
                    else { this.setState({ showMore: false }) }

                }
                this.setState({ contentLoader: false })
            })
        }
    }


    postContent = (item) => {
        if(checkPermission('publish', 'email_camp', true)){
            let { campaignLayout, selectContent, addPostIds, layout } = this.state;
            const isAvailable = campaignLayout && campaignLayout.length ? campaignLayout.findIndex((x) => x.postFile === item.postFile) : -1;
            var postlnth = layout == 1 ? 4 : layout == 2 ? 6 : layout == 3 ? 9 : layout == 4 ? 4 : '';
            if (isAvailable == -1 && campaignLayout?.length < postlnth) {
                this.setState({ is_changes: true })
                const modifiedItem = { ...item, isAdded: true, active: true };
                campaignLayout.push(modifiedItem);
                addPostIds.push(item.postFile);
                selectContent.map((x) => {
                    if (x && x.postFile === item.postFile && !x.isAdded) {
                        x.active = true;
                        x.isAdded = true;
                    }
                });
            } else {
                const campaignIndex = campaignLayout && campaignLayout.length ? campaignLayout.findIndex((x) => x.postFile === item.postFile) : -1;
                if (campaignIndex !== -1) campaignLayout.splice(campaignIndex, 1);

                selectContent.map((x) => {
                    if (x && x.postFile === item.postFile) {
                        x.active = false;
                        x.isAdded = false;
                    }
                });
                const postIdIndex = addPostIds.indexOf(item.postFile);
                if (postIdIndex !== -1) addPostIds.splice(postIdIndex, 1);
            }
            this.setState({ campaignLayout: campaignLayout, addPostIds: addPostIds, selectContent: selectContent, contentLoading: false })
        }

    };

    updateData = (publish) => {
        const { userData } = this.props
        const { campaignLayout, layout } = this.state
        const postData = layout == 1 ? 4 : layout == 2 ? 6 : layout == 3 ? 9 : layout == 4 ? 4 : '';
        if (campaignLayout && campaignLayout?.length >= postData) {
            try {
                const extra_data = userData.user.extra_data ? JSON.parse(userData.user.extra_data) : {};
                if (publish) this.setState({ preview: true })
            } catch (error) {
                console.log('error', error)
            }
        }
        else this.setState({ postError: true, spinner: false })

    }


    saveSelectedPost = () => {
        const { campaignLayout, spaceVal, layout, activeWall } = this.state
        var postlnth = layout == 1 ? 4 : layout == 2 ? 6 : layout == 3 ? 9 : layout == 4 ? 4 : '';
        this.updateData()
        this.setState({ spinner: true, generateCode: "" }, () => {
            var selectedPostIds = []
            if (campaignLayout && campaignLayout?.length >= postlnth) {
                this.setState({ postError: false })
                campaignLayout.slice(0, postlnth).forEach(post => selectedPostIds.push(post.postFile));
                var obj = { spacing: spaceVal, theme_type: layout, wallId: activeWall.id, post_id: selectedPostIds.join(',') }
                this.props.updateMail({ ...obj }, () => this.updateResponse(obj))

            } else {
                this.setState({ postError: true, spinner: false })
            }
        })
    }

    updateResponse = (obj) => {
        this.setState({ gridData: obj, preview: true, spinner: false, is_changes: false })
    }

    closeAside = () => this.setState({ filterToggle: false })

    updatePlatform = (value) => {
        const { userData } = this.props
        this.setState({ selectedPlatform: value })
    }

    render() {

        const { activeCamp, activeWall, spaceVal, layout, selectContent, campaignLayout, selectedPlatform, preview, postError, filterToggle, contentLoader, selectLoader, showMore } = this.state
        const { loader, allWalls, emailList, toggle, userData } = this.props
        const authData = userData?.sessionData
        const websiteUrl = authData?.createFrom.toLowerCase() == 'shopify' ? `shopify` : authData?.createFrom.toLowerCase() == 'bigcommerce' ? `bigcommerce` : 'publish'
        const { navigate } = this.props

        return <div className='d-flex flex-column flex-column-fluid full-content'>
            <div className="subheader">
                <PublishSubheader websiteUrl={websiteUrl}/>
            </div>
            <Container fluid>
                {emailList && emailList.length > 0 ? <Card className="content_inner email_camp border-0">
                    <Card.Body className='p-0 d-flex w-100'>
                        <CardAside filterToggle={filterToggle} closeAside={this.closeAside}>
                            {!preview ?
                                <Card className='a_header_ a_footer_ w-100'>
                                    <Card.Header className='py-4'>
                                        <WebsiteSwitcher contentList={emailList} changeWebsite={this.setEmailData} selectedContent={activeCamp} title="Email Campaign" method={this.updateEmail} />
                                    </Card.Header>
                                    <Card.Body className='bg-gray-100 aside_body p-0 w-100'>
                                        <div className='border-bottom p-4 mb-4'>
                                            <h5>Select Content</h5>
                                            <ContentSwitcher contentList={allWalls} modifyContent={this.changeWall} selectedContent={activeWall} title="Email" onlySwitcher={true} selectLoader={selectLoader} />
                                        </div>
                                        <AsideContent spaceVal={spaceVal} layout={layout} selectLayout={this.selectLayout} changeSpace={this.changeSpace} postError={postError} />
                                    </Card.Body>
                                    <Card.Footer className='d-flex justify-content-end py-3'>
                                        <div className="">
                                            {this.state.is_changes ?
                                                <Button id="mail_save_btn" className={this.state.spinner ? `spinner` : null} onClick={() => this.saveSelectedPost()}>
                                                    Save and Publish
                                                </Button> :
                                                <Button id="mail_publish_btn" className="btn-min-w" onClick={() => this.updateData(true)}>
                                                    Publish
                                                </Button>}
                                        </div>
                                    </Card.Footer>
                                </Card>
                                :
                                <Card className='a_footer_ w-100'>
                                    <Card.Body className='bg-gray-100 aside_body p-0 w-100'>
                                        <PlatformAside selectedPlatform={selectedPlatform} onSelectedPlatform={(value) => this.updatePlatform(value)} />
                                    </Card.Body>
                                    <Card.Footer className='py-3'>
                                        <div className="d-flex justify-content-between">
                                            <Button id="mail_back_btn" variant="link" className="text-muted" onClick={() => this.setState({ preview: false })}>
                                                <FontAwesomeIcon icon={'arrow-left'} />
                                                Back
                                            </Button>
                                            <Button id="mail_edit_btn" className="btn-min-w" onClick={() => this.setState({ preview: false })}>
                                                Edit
                                            </Button>
                                        </div>
                                    </Card.Footer>
                                </Card>
                            }
                        </CardAside>
                        {!preview ?
                            <Card className="preview_ p_header_">
                                <Card.Header className="py-0">
                                    <Row className="gx-8" style={{ width: 'calc(100% + 2rem)' }}>
                                        <Col xs={4} sm={5} xl={6} className="f-between border-end bg-gray-100">
                                            <span className='fs-8 fw-medium mb-0 text-muted'>Select posts to embed in your email gallery</span>
                                        </Col>
                                        <Col xs={8} sm={7} xl={6} className="f-center">
                                            <div className="f-between w-100">
                                                <span className='h5 mb-0 '>Preview</span>
                                                <div>
                                                    <Button id="toggle_aside" variant='link' size='sm' className='d-flex d-xl-none' onClick={() => this.setState({ filterToggle: !filterToggle })}>
                                                        Menu
                                                        <FontAwesomeIcon icon={'sliders'} iconClass={'ms-2 lh-base'} />
                                                    </Button>
                                                </div>
                                            </div>
                                        </Col>
                                    </Row>
                                </Card.Header>
                                <Card.Body className='p-0 overflow-hidden'>
                                    <Row>
                                        <Col xs={4} sm={5} xl={6} className="bg-gray-100 card-scroll-h-b position-relative">
                                            {contentLoader ?
                                                <SkGallery />
                                                : selectContent.length > 0 ?
                                                    <Gallery activeWall={activeWall} selectContent={selectContent} getPosts={this.getPosts} postContent={this.postContent} showMore={showMore} />
                                                    :
                                                    <div className="h-100 f-center">
                                                        <EmptyWithIcon title={'Preview Missing? 👀'} desc={'You skipped the preliminary step. Connect your social account to collect content and begin creating your gallery.'} icon={'media'} button={{ action: () => this.props.navigate(`/content/moderation/${localStorage.getItem('wallId') ? localStorage.getItem('wallId') : ''}`), text: 'Add Content' }} isFullScreen={false} />
                                                    </div>
                                            }

                                        </Col>
                                        <Col xs={8} sm={7} xl={6}>
                                            <div className="w-100 pe-4 py-4 card-scroll-h-b f-center flex-wrap">
                                                <div className="mx-auto w-100" style={{ maxWidth: 400 }}>
                                                    <Preview layout={layout} campaignLayout={campaignLayout} spaceVal={spaceVal} />
                                                </div>
                                            </div>
                                        </Col>
                                    </Row>
                                </Card.Body>
                            </Card>
                            :
                            <CodePreview selectedPlatform={selectedPlatform} campaignLayout={campaignLayout} layout={layout} spaceVal={spaceVal} />
                        }
                    </Card.Body>
                </Card>
                    :
                    loader ? <div style={{ minHeight: 'calc(100vh - 170px)' }}>
                        <Loader />
                    </div>
                        :
                        <div className='f-center' style={{ minHeight: 'calc(100vh - 50px)' }}>
                            <EmptyWithIcon title={'Create an email, to start! 👀'} desc={'Oops! Looks like you have deleted your email campaign, create one by simply clicking on the button below.'} icon={'email'} button={{ action: () => checkPermission('publish', 'email_camp', true) ? this.props.TOGGLE_ADD_EMAIL() : {}, text: 'Create Email' }} isFullScreen={true} />
                            <WebsiteSwitcher title="Email Campaign" method={this.updateEmail} addNew={toggle} noList={true} />
                        </div>}
            </Container>
        </div>

    }
}

const mapStateToProps = (state) => {
    return {
        allWalls: state.dashboardData?.dashboarddata?.allWalls,
        userData: state.authorized?.userData?.user_data,
        emailList: state.emailList?.emailList,
        loader: state.emailList?.emailLoading,
        toggle: state.emailList?.addEmailNow
    }
}

const mapDispatchToProps = dispatch => {
    return {
        updateMail: (obj, callback) => dispatch(UPDATE_EMAIL_DATA(obj, callback)),
        getUserInfo_afterChanges: () => dispatch(getUserInfo_afterChanges()),
        TOGGLE_ADD_EMAIL: () => dispatch({ type: TOGGLE_ADD_EMAIL, payload: true }),
        getEmailList: () => dispatch(GET_ALL_EMAIL_LIST()),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(EmailCampaign))