import { Row, Col } from 'react-bootstrap';
import { generateRandomArray } from '../../../../../utils';
const SkGallery = () => {
    return <div className="p-3 w-100">
        <Row className="gx-1">
            {generateRandomArray(16).map((number, index) => <Col key={index} sm={4} lg={3} xl={4} xxl={3}>
                <p className="sk_rect w-100 mb-1" style={{ paddingBottom: '100%' }}></p>
            </Col>)}
        </Row>
    </div>

}
export default SkGallery;
