import { generateRandomArray } from '../../../../../../utils';
const GridFooter = () => {
       return <div className="emailPreview_footer f-center flex-column w-100 p-4 mt-3">
              {generateRandomArray(3).map((bar, index) => <div key={index} className={`w-${index > 1 ? `75` : `100`}  bg-gray-200 rounded-4 h-8px mb-${index > 1 ? `3` : `2`}`}></div>)}
              <div className="f-center mb-3">
                     {generateRandomArray(5).map((number, index) => (<div key={index} className="h-22px w-22px mx-1 rounded-circle bg-gray-200"></div>))}
              </div>
              <div className="w-100 rounded-4 h-8px bg-gray-200" style={{ maxWidth: 90 }}></div>
       </div>

}

export default GridFooter;
