import { Component } from "react";
import { Button, Form, Row, Col, Image } from "react-bootstrap";
import { baseUrl, FontAwesomeIcon } from "../../../../../Assets/AssetsHelper";
import { connect } from "react-redux";
import RangeInput from "../../CommonComponents/RangeInput";
import { checkPermission } from "../../../../../actions/collaboratorActions";
import RequestButtonHOC from "../../CommonComponents/RequestButtonHOC";

class AsideContent extends Component{


    render(){
        const {spaceVal, layout, changeSpace, selectLayout, postError} = this.props
        const GridSize = ["2X2", "2X3", "3X3", "1X4"];
        
        return <>
                <div className='border-bottom px-4 py-4 mb-4'>
                    <div className="mb-3">
                        <Form.Label htmlFor="mail_range">Spacing</Form.Label>
                        <RangeInput id="mail_range" min={0} max={20} step={1} value={spaceVal} onChange={(e)=>changeSpace(e)} inheritStyle={!checkPermission('publish', 'email_camp',true,true)}/>
                    </div>
                </div>
                <div className='px-4 pb-3 mb-4'>
                    <Form.Label>Choose the Layout</Form.Label>
                    <Row className="select-group flex-wrap row gx-8 border-top">
                        {GridSize.map((gird, index) => (
                            <Col className="col-4 p-0 select-grid">
                                <Button key={index} variant="grid" data-grid={gird} className={`mail_layout rounded-0 d-flex align-items-center flex-column py-4 border-0 w-100 ${layout == index+1 ? 'active' : ''}`} onClick={() => selectLayout(index+1)} disabled={!checkPermission('publish', 'email_camp',true,true)}>
                                    <div className="w-50px h-50px mb-2">
                                        <Image className="invert" src={baseUrl(`media/icons/email-layout/${index+1}.svg`)} height={46} width={150} alt={gird} fluid/>
                                    </div>
                                    <p className="mb-0 text-center">{gird} Grid</p>
                                </Button>
                            </Col>
                        ))}
                        <Col className="col-4 p-0 select-grid">
                            <RequestButtonHOC title={'Request New Grid'}>
                                <Button variant="grid" className={`rounded-0 d-flex align-items-center flex-column py-4 px-0 border-0 w-100`}>
                                    <div className="w-50px h-30px mb-2 f-center">
                                        <FontAwesomeIcon type={'light'} icon={'plus-circle'} iconClass={'fs-1 text-gray-400 me-0'}/>
                                    </div>
                                    <p className="mb-0 text-center">Request New Grid</p>
                                </Button>
                            </RequestButtonHOC>
                        </Col>
                    </Row>
                    {postError ? <p id="mail_select_e" className="invalid-feedback d-block">*Please select posts and fill the grid to continue</p> : ''}
                </div>

            </>
    }
}

const mapStateToProps = (state) => {
    return {
        allWalls: state.dashboardData?.dashboarddata?.allWalls,
        // userData: state.authorized?.userData,
        // active_email: state.emailList?.active_email,
        // emailList: state.emailList?.emailList,
        // loader: state.emailList?.emailLoading,

    }
}

export default connect(mapStateToProps) (AsideContent)