import React, { Component } from 'react';
import {Form} from 'react-bootstrap';

class RangeInput extends Component {
       render() {
              const {id, min, max, step, value, inheritStyle} = this.props
              return (<div className='d-flex flex-column'>
                            <Form.Range id={id} min={min} max={max} step={step} value={value} name='range' onChange={(e)=>this.props.onChange(e.target.value)} disabled={inheritStyle}/>
                            <p className='d-flex justify-content-between mb-0 fs-9 text-muted fw-medium'><span data-min={id}>{min}</span><span data-value={id}>{value}</span></p>
                     </div>
              );
       }
}

export default RangeInput;