import { Component } from "react";
import Grid_2x2 from "./Components/Grid/Grid_2x2";
import Grid_2x3 from "./Components/Grid/Grid_2x3";
import Grid_3x3 from "./Components/Grid/Grid_3x3";
import Grid_4x1 from "./Components/Grid/Grid_4x1";

class Preview extends Component{

    emailPreview = (size) => {
        const { campaignLayout, spaceVal } = this.props

        switch (size) {
            case 1:
                return <Grid_2x2 data={campaignLayout} spaceVal={spaceVal} />;
            case 2:
                return <Grid_2x3 data={campaignLayout} spaceVal={spaceVal} />;
            case 3:
                return <Grid_3x3 data={campaignLayout} spaceVal={spaceVal} />;
            case 4:
                return <Grid_4x1 data={campaignLayout} spaceVal={spaceVal} />;
            default:
                return <Grid_2x2 data={campaignLayout} spaceVal={spaceVal} />;
        }
    }
    render(){
        const { layout } = this.props
        return this.emailPreview(layout)
    }
}

export default Preview