import { Component } from "react";
import { Button, Tab, Tabs, Card, Nav, OverlayTrigger, Tooltip } from "react-bootstrap";
import Grid_2x2_Code from "./Components/GridCode/Grid_2x2_Code";
import Grid_2x3_Code from "./Components/GridCode/Grid_2x3_Code";
import Grid_3x3_Code from "./Components/GridCode/Grid_3x3_Code";
import Grid_4x1_Code from "./Components/GridCode/Grid_4x1_Code";
import Klaviyo from './Components/EmailPlatform/Klaviyo';
import Omnisend from './Components/EmailPlatform/Omnisend';
import Hubspot from './Components/EmailPlatform/Hubspot';
import ActiveCampaign from './Components/EmailPlatform/ActiveCampaign';
import Mailchimp from './Components/EmailPlatform/Mailchimp';
import GetResponse from './Components/EmailPlatform/GetResponse';
import Other from './Components/EmailPlatform/Other';
import { COPY_TO_CLIPBOARD, OPEN_INTERCOM } from "../../../../utils";
import Preview from "./Preview";
import { Link } from "react-router-dom";
import { baseUrl, FontAwesomeIcon } from "../../../../Assets/AssetsHelper";
import ShareCodeModal from "../CommonComponents/ShareCodeModal";


class CodePreview extends Component {

    state = { copied: false, openPopup: false }

    setOpenPopup = (status) => {
        this.setState({ openPopup: false })
    }

    copyText = (id) => {
        COPY_TO_CLIPBOARD(id)
        this.setState({ copied: true })
        setTimeout(() => { this.setState({ copied: false }) }, 1000)
    }

    emailPlatform = (platform) => {
        switch (platform) {
            case "klaviyo":
                return <Klaviyo />;
            case "omnisend":
                return <Omnisend />;
            case "hubspot":
                return <Hubspot />;
            case "active":
                return <ActiveCampaign />;
            case "mailchamp":
                return <Mailchimp />;
            case "getresesponse":
                return <GetResponse />;
            case "other":
                return <Other />;
            default:
                return <Klaviyo />;
        }
    }

    emailViewCode = (gridType) => {
        const { campaignLayout, spaceVal } = this.props
        const errorMessage = "Please select a different layout since the saved campaign contains fewer images."
        switch (gridType) {
            case 1:
                return <Grid_2x2_Code data={campaignLayout} spaceVal={spaceVal} />
                break;
            case 2:
                return <Grid_2x3_Code data={campaignLayout} spaceVal={spaceVal} />;
                break;
            case 3:
                return <Grid_3x3_Code data={campaignLayout} spaceVal={spaceVal} />
                break;
            case 4:
                return <Grid_4x1_Code data={campaignLayout} spaceVal={spaceVal} />;
                break;
            default:
                return <Grid_4x1_Code data={campaignLayout} spaceVal={spaceVal} />;
                break;
        }
    }

    render() {
        const { twoBytwo, twoBythree, threeBythree, oneByfour, generateCode, selectedPlatform, layout, campaignLayout, spaceVal } = this.props;
        const { copied, openPopup } = this.state
        return <Card className="preview_ p_header_">
            <Tab.Container id="left-tabs-example" defaultActiveKey="code">
                <Card.Header className="pb-0">
                    <Nav variant="underline" className="align-items-end">
                        <Nav.Item className="me-3">
                            <Nav.Link eventKey="code">Genrated Code</Nav.Link>
                        </Nav.Item>
                        <Nav.Item>
                            <Nav.Link eventKey="preview">Preview</Nav.Link>
                        </Nav.Item>
                    </Nav>
                </Card.Header>
                <Tab.Content>
                    <Tab.Pane eventKey="code">
                        <Card.Body className="pb-10">
                            <div className="mb-10">
                                <h5 className="mb-6">Publish On HTML Website</h5>
                                <div className="d-flex align-items_center mb-5">
                                    <span className="h-22px w-22px bg-primary text-white rounded-circle text-center me-2 fw-bold f-center" style={{ marginTop: -2 }}>1</span>
                                    <h6 className="mb-0">Copy the generated email campaign code</h6>
                                </div>
                                <div className="position-relative mb-7 embed_code embed_code_box iframe_embcode_box rounded-3">
                                    <div className='embed-border p-2'>
                                        <code className="form-control textarea-min-row-4 p-lg-4 fs-9 embed-border" id="emailEmbedCode_" onClick={() => { this.copyText('emailEmbedCode_') }}>
                                            {this.emailViewCode(layout)}
                                        </code>
                                    </div>
                                    <div className="position-absolute end-0 bottom-0 pe-5 pb-5">
                                        <OverlayTrigger placement="top" overlay={<Tooltip>{!copied ? 'Copy Code' : 'Code Copied'}</Tooltip>}>
                                            <Button variant="link" className="fs-6 me-4 text-gray-700" onClick={() => this.copyText('emailEmbedCode_')}>
                                                <FontAwesomeIcon type={'solid'} icon={!copied ? 'copy' : 'check'} iconClass={copied ? 'text-success' : ''} />
                                            </Button>
                                        </OverlayTrigger>
                                        <OverlayTrigger placement="top" overlay={<Tooltip>Share Code</Tooltip>}>
                                            <Button variant="link" className="text-gray-700 fs-6 GetShareCode" onClick={(e) => this.setState({ openPopup: true })}>
                                                <FontAwesomeIcon type={'solid'} icon={'envelope'} iconClass="me-0" />
                                            </Button>
                                        </OverlayTrigger>
                                    </div>
                                </div>
                                <div className="d-flex align-items_center mb-5">
                                    <span className="h-22px w-22px bg-primary text-white rounded-circle text-center me-2 fw-bold f-center" style={{ marginTop: -2 }}>2</span>
                                    <h6 className="mb-0">Paste the generated code into the email template</h6>
                                </div>
                                {this.emailPlatform(selectedPlatform)}
                            </div>
                            <Card.Footer className="f-center justify-content-start position-absolute bottom-0 start-0 w-100 bg-body" style={{ height: 67 }}>
                                <div className="py-1 px-3 rounded-pill badge-light-primary text-nowrap d-flex flex-wrap align-items-center">
                                    <p className="my-1 text-muted d-md-inline d-none">Don’t know how to install code? Reach out to our support team</p>
                                    <p className="my-1 text-muted d-md-none d-inline">Help</p>
                                    <FontAwesomeIcon icon={'angle-right'} iconClass="mx-2" />
                                    <Button variant='link' className='f-center d-flex' onClick={OPEN_INTERCOM}>
                                        <FontAwesomeIcon type={'solid'} icon={'comments'} iconClass='me-1' />
                                        Connect to Support
                                    </Button>
                                </div>
                            </Card.Footer>
                        </Card.Body>
                    </Tab.Pane>
                    <Tab.Pane eventKey="preview">
                        <Card.Body className="f-center flex-wrap">
                            <div className="mx-auto w-100 p-3 border rounded-1" style={{ maxWidth: 500 }}>
                                <Preview layout={layout} campaignLayout={campaignLayout} isCreated={true} spaceVal={spaceVal} />
                            </div>
                        </Card.Body>
                    </Tab.Pane>
                </Tab.Content>
            </Tab.Container>
            {openPopup ? <ShareCodeModal type={1} text="Share this embed code with your team. Enter the email Id to send the code directly to them" heading="Send code by email" onClose={(e) => this.setState({ openPopup: false })} codeId={'#emailEmbedCode_'} /> : null}
        </Card>



    }
}

export default CodePreview