import React from 'react';

const Mailchimp = () => {
    return  <ul className="list-unstyled list-points fs-8 ms-3">
                <li className="mb-3">Login to your <a className='fw-semibold' href='http://mailchimp.com/' target='_blank' title='Mailchimp'>Mailchimp</a> Account. To create an email campaign, click on the <span className="fw-semibold">Create icon</span></li>
                <li className="mb-3">From the email dropdown click on <span className="fw-semibold">Regular</span> and give a name to your campaign</li>
                <li className="mb-3">Next, add recipients and in the subject field enter a subject line for your email</li>
                <li className="mb-3">Now design your email by choosing the template</li>
                <li className="mb-3">Add HTML from the content block and paste the copied UGC campaign code here</li>
                <li>Apply the required changes and Voila! You have successfully added the UGC campaign on email</li>
            </ul>

}

export default Mailchimp;