import React from 'react';

const Hubspot = () => {
    return  <ul className="list-unstyled list-points fs-8 ms-3">
                <li className="mb-3">Login to your HubSpot Account <a className='fw-semibold' href='https://www.hubspot.com/' target='_blank' title='HubSpot'>HubSpot Account</a>.</li>
                <li className="mb-3">Now, navigate to Marketing {'>'} Email</li>
                <li className="mb-3">Next, click on Create email</li>
                <li className="mb-3">From the drag and drop tab, choose a template based on the type of email you want to create</li>
                <li className="mb-3">To add UGC to your template, click and drag a HTML element from the Content tab in the left panel directly into the email editor</li>
                <li className="mb-3">Now, paste the generated UGC email campaign code</li>
                <li>Lastly, apply the required changes and Voila! You will have successfully added the UGC campaign on email</li>
            </ul>

}

export default Hubspot;